<template>
  <div>
    <div v-loading="g_loading" style="width: calc(100% - 10px);height:calc(100vh - 140px);">
      <SeeksRelationGraph ref="seeksRelationGraph" :options="graphOptions" :on-node-expand="onNodeExpand" />
    </div>
  </div>
</template>

<script>
import SeeksRelationGraph from 'relation-graph'
export default {
  name: 'DrawTree',
  components: { SeeksRelationGraph },
  data() {
    return {
      g_loading: true,
      graphOptions: {
        'layouts': [
          {
            'label': '中心',
            'layoutName': 'tree',
            'layoutClassName': 'seeks-layout-tree',
            'defaultJunctionPoint': 'border',
            'defaultNodeShape': 0,
            'defaultLineShape': 1,
            'centerOffset_x': -300,
            'centerOffset_y': 0,
            'min_per_width': '60',
            'min_per_height': '400'
          }
        ],
        "defaultExpandHolderPosition": "bottom",
        'defaultLineShape': 4,
        'defaultJunctionPoint': 'tb',
        'defaultNodeShape': 1,
        'defaultNodeWidth': '50',
        'defaultNodeHeight': '250',
        'defaultNodeBorderWidth': 0,
        'defaultNodeColor': " #f8e3c5",
        'defaultNodeFontColor': '#606266',
        'defaultLineColor': '#606266'
      }
    }
  },
  created() {
  },
  mounted() {
    this.setGraphData({'rootId':1, 'nodes':[{'id':1, 'text':'', 'color':"white"}], 'links':[]})
  },
  methods: {
    setGraphData(data) {
      // console.log(data)
      var __graph_json_data = data;

      // console.log(JSON.stringify(__graph_json_data))
      __graph_json_data.nodes.forEach(thisNode => {
          thisNode.width = 100;
          thisNode.height = 50;
      })

      setTimeout(function() {
        this.g_loading = false
        this.$refs.seeksRelationGraph.setJsonData(__graph_json_data, (seeksRGGraph) => {
          // 这些写上当图谱初始化完成后需要执行的代码
        })
      }.bind(this), 1000)
    },
    onNodeExpand(node, e) {
      //模拟动态加载数据
      if (node.data && node.data.asyncChild === true && node.data.loaded === false) {
        this.g_loading = true
        node.data.loaded = true
        setTimeout(function() {
          this.g_loading = false
          var _new_json_data = {
            nodes: [
              { id: node.id + '-child-1', text: node.id + '-的子节点1'},
              { id: node.id + '-child-2', text: node.id + '-的子节点2'},
              { id: node.id + '-child-3', text: node.id + '-的子节点3'}
            ],
            links: [
              { from: node.id, to: node.id + '-child-1', text: '动态子节点'},
              { from: node.id, to: node.id + '-child-2', text: '动态子节点'},
              { from: node.id, to: node.id + '-child-3', text: '动态子节点'}
            ]
          }
          this.$refs.seeksRelationGraph.appendJsonData(_new_json_data, (seeksRGGraph) => {
            // 这些写上当图谱初始化完成后需要执行的代码
          })
        }.bind(this), 1000)
        return
      }
    }
  }
}
</script>

