<template>
  <div class="hello" >
    <el-container class="layout-container" >
      <!--头部信息-->
      <el-header class="el-header" >
        <div class="title">
          <span>ARENA</span>
        </div>
      </el-header>
      <!--侧栏信息-->
      <el-container>
        <el-aside width="300px">
          <el-scrollbar>
            <el-menu active-text-color="#409EFF" >
              <el-submenu index="1">
                <template #title>
                  <i class="el-icon-s-data"></i>
                  <span>Database Information</span>
                </template>
                <el-submenu v-for="(value, table) in tableList" :key="table" :index="table">
                  <template #title>{{ table }}</template>
                  <el-menu-item v-for="(colType, col) in value" :key="col" :index="col">
                    {{ col }} ({{ colType }})
                  </el-menu-item>
                </el-submenu>
              </el-submenu>

              <el-submenu index="4">
                <template #title>
                  <i class="el-icon-s-promotion"></i>
                  <span>Feedback</span>
                </template>
                <el-menu-item-group class="feedbackGroup">
                  <template slot="title">The most valuable plan</template>
                  <el-menu-item index="4-1-1">
                    <el-input placeholder="plan id" v-model="bestPlanId" size="mini" style="font-size: 13px">
                      <template slot="prepend">Plan</template>
                    </el-input>
                  </el-menu-item>
                  <el-menu-item index="4-1-2" class="noHighLight">
                    <el-input type="textarea" :rows="5" placeholder="Please input the reason." v-model="bestPlanReason" style="font-size: 13px">
                    </el-input>
                  </el-menu-item>
                </el-menu-item-group>

                <el-menu-item-group class="feedbackGroup">
                  <template slot="title">The least valuable plan</template>
                  <el-menu-item index="4-2-1">
                  <el-input placeholder="plan id" v-model="worstPlanId" size="mini" style="font-size: 13px">
                    <template slot="prepend">Plan</template>
                  </el-input>
                  </el-menu-item>
                  <el-menu-item index="4-2-2" class="noHighLight">
                    <el-input type="textarea" :rows="5" placeholder="Please input the reason." v-model="worstPlanReason" style="font-size: 13px">
                    </el-input>
                  </el-menu-item>
                </el-menu-item-group>

                <el-menu-item-group>
                  <template slot="title">Submit your feedback</template>
                  <el-menu-item index="4-3-1">
                    <el-button size="mini">Submit</el-button>
                  </el-menu-item>
                </el-menu-item-group>
              </el-submenu>

              <el-submenu index="2">
                <template #title>
                  <i class="el-icon-s-tools"></i>
                  <span>Setting</span>
                </template>
                  <el-menu-item index="2-1">
                      <el-switch v-model="isComparePlan" active-text="Compare Plan"></el-switch>
                  </el-menu-item>
              </el-submenu>

              <el-submenu index="3">
                <template #title>
                  <i class="el-icon-question"></i>
                  <span>Help</span>
                </template>
                <el-menu-item index="3-1">
                  <router-link tag='a' target='_blank' to="about" >
                  About
                  </router-link>
                </el-menu-item>
              </el-submenu>
            </el-menu>
          </el-scrollbar>
        </el-aside>
        <!--主体信息-->
        <el-main>
          <el-row :gutter="20" class="el-row">
            <el-col :span="9" class="el-col">

              <el-row class="el-row">
                <el-input type="textarea" placeholder="Input your SQL or select an example SQL" :autosize="{minRows:10, maxRows:20}" v-model="SQLstatement"  />
              </el-row>

              <!--                            选择框 和 提交按钮-->
              <el-row :gutter="10" class="el-row">
                <el-col :span="7" class="el-col">
                  <el-select v-model="selectedSQL" @change="loadSQLFile($event)" @clear="clearTextarea" placeholder="Example SQL" clearable>
                    <el-option v-for="singleFile in SQLFiles" :value="singleFile" :key="singleFile">
                      {{singleFile}}
                    </el-option>
                  </el-select>
                </el-col>

                <el-col :span="2" class="el-col">
                  <el-button :loading="isLoading" @click="submitSQL" type="primary">Execute</el-button>
                </el-col>
              </el-row>
              <el-row class="el-row">
                <el-col class="el-col">
                  <el-card v-if="tableData.length > 0" class="box-card" shadow="hover">
                    <el-table v-if="tableData.length > 0"
                              :data="tableData"
                              highlight-current-row
                              @current-change="handleTableSelect"
                              style="width: 100%"
                              :default-sort="{prop:'number'}"
                              stripe>
                      <el-table-column prop="number" label="Id" sortable></el-table-column>
                      <el-table-column prop="cost" label="Cost" sortable></el-table-column>
                      <el-table-column prop="s_dist" label="S_dist" sortable></el-table-column>
                      <el-table-column prop="c_dist" label="C_dist" sortable></el-table-column>
                      <el-table-column prop="cost_dist" label="Cost_dist"></el-table-column>
                    </el-table>
                  </el-card>
                </el-col>
              </el-row>
            </el-col>
            <el-col :span="15" class="el-col">
              <el-card class="box-card" v-show="needToDraw != null">
                <draw-tree ref="drawTree"></draw-tree>
              </el-card>
            </el-col>
          </el-row>

        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import DrawTree from "@/components/DrawTree";
export default {
  name: 'HelloWorld',
  components: {DrawTree},
  data () {
    return {
      dbName: 'imdb',
      tableList: {},
      SQLstatement: '',
      selectedSQL: '',
      SQLFiles: [],
      isLoading: false,
      tableData: [],
      isComparePlan: false,
      bestPlanId: null,
      bestPlanReason: '',
      worstPlanId: null,
      worstPlanReason: '',
      needToDraw: null,
      aboutURL: '',
      helpURL: '',
    }
  },
  methods: {
    getSQLFiles() {
      console.log(process.env.BASE_API)
      this.$axios.get("/sqlFile")
        .then((response)=> {
          for(var i in response.data){
            this.SQLFiles.push(response.data[i]);
          }
          this.SQLFiles.sort(function (a,b) {
            return Number(a.split('.')[0]) - Number(b.split('.')[0]);
          })
        })
        .catch((error)=> {
          console.log("获取sql文件列表失败");
          console.log(error);
          this.SQLFiles = [];
        });
    },

    getDBInfo() {
      this.$axios.get("/databaseInfo/"+this.dbName )
        .then((response)=> {
          this.tableList = JSON.parse(response.data)
        })
        .catch((error)=> {
          console.log("获取数据库信息失败");
          console.log(error);
          this.tableList = {"请求数据库失败" : {"Cat't": "get dbInfo"}}
        });

    },

    /**
     * 清空文本框区域的所有内容
     */
    clearTextarea() {
      this.SQLstatement = '';
    },

    loadSQLFile($event) {
      // 因为这个影响这个函数的是选择框的改变事件，并且该选择框提供了清空的选项
      // 所以存在 this.selectedSQL 从有内容变为无内容的情形
      if (this.selectedSQL.length > 0){
        // console.log(this.selectedSQL);
        var targetURL = '/sqlFile/' + this.selectedSQL;

        this.$axios.get(targetURL )
          .then((response)=> {
            // console.log(response)
            this.SQLstatement = response.data;
          })
          .catch((error)=> {
            console.log("请求sql文件内容失败, 目标sql文件为：" + this.selectedSQL);
            console.log(error);
            this.SQLstatement = "I can't load the sql file ╮(╯-╰)╭";
          });
      }
    },

    submitSQL() {
      console.log("点击按钮");
      // 首先判断当前是否可以发送请求
      if (this.SQLstatement === ''){  // 输入语句为空
        this.$message({message: 'Please input the SQL!', showClose:true, type:'error'});
        return;
      }

      var formData = new FormData();
      var statement = this.SQLstatement.toLowerCase();
      statement = statement.replace(/\s+/g, ' ');
      formData.append('statement', statement);
      formData.append('fileName', this.selectedSQL);

      this.$axios.post('/search', formData, {
        header: {
          'Content-Type': 'application/x-www-form-urlencoded',
        }
      }).then((response)=> {
        console.log('发送SQL查询请求成功!');
        console.log(typeof(response.data));
        // var temp_data;
        // for (var i=0;i<response.data.length;i++)
        // {
        //   temp_data = JSON.parse(response.data);
        //
        // }
        this.tableData = response.data;
      }).catch((error)=> {
          console.log('发送SQL查询请求失败!');
          console.log(error);
      })
    },

    handleTableSelect(val) {
      // val 是一个对象，即表格每一行的对象，转换为相应的格式后，可以直接绘制树形图
      console.log(val);
      this.needToDraw = {rootid: '1', nodes:[{id:'1', text:'root'}, {id:'2', text:'child'}], links:[{from:'1', to:'2'}]}
      this.$refs.drawTree.setGraphData(this.needToDraw)
    }
  },
  mounted() {
    this.getSQLFiles(); // 在初始化时首先获得所有的 sql 文件
    this.getDBInfo();
    var list;
    list = window.location.href.split('/').slice(0, 3);
    this.aboutURL = list.join('/')+'/about';
    this.helpURL = list.join('/') +'/help';
  }
}
</script>

